import p5 from 'p5'

let rectArr: SingleRect[] = []

const gradient = (p: p5) => {
    p.setup = () => {
        p.createCanvas(p.windowWidth, p.windowHeight)
        p.background(0)
        p.smooth()
        // p.translate(p.windowWidth / 2, p.windowHeight / 2)
        setGradient(p)
    }
    p.draw = () => {
        rectArr.forEach(r => {
            r.stretch()
            r.display(p)
        })
    }
    p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight)
        rectArr = []
        setGradient(p)
    }
}

const setGradient = (p: p5) => {
    const fColor = p.color(0)
    const tColor = p.color(255)
    for (let x = 0; x < p.windowWidth / 4; x++) {
        const c = p.lerpColor(fColor, tColor, Math.random())
        const r = new SingleRect(x * 4, 0, c)
        rectArr.push(r)
    }
}

class SingleRect {
    private maxW = 5
    private nextW: number = Math.random() * this.maxW + 0.1
    private currentW = Math.random() * this.maxW + 0.1
    private x: number
    private y: number
    private color: p5.Color
    constructor(x: number, y: number, color: p5.Color) {
        this.x = x
        this.y = y
        this.color = color
    }

    display = (p: p5) => {
        p.stroke(this.color)
        p.strokeWeight(this.currentW)
        p.line(this.x, this.y, this.x, p.windowHeight)
    }
    stretch = () => {
        this.currentW += (this.nextW - this.currentW) * 0.01
        if (Math.abs(this.currentW - this.nextW) < 0.1) {
            this.nextW = Math.random() * this.maxW + 0.1
        }
    }
}



export default gradient